.modalWrapper {
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
}

.modalContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  min-width: 400px;
  min-height: 200px;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgb(109, 102, 102);
  background-color: #fff;
  padding: 10px 20px;
}

.closeButton {
  position: absolute;
  top: 5px;
  right: 5px;
}

.actionButtons {
  display: flex;
  gap: 5px;
  justify-content: flex-end;
}

.title {
  font-size: 20px;
}

.text {
  margin: auto;
}
